.backdrop {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 50;
}

#modal {
  width: 50rem;
  max-width: 95%;
  background: $color-white;
  border-radius: $border-radius;
  overflow: hidden;
  text-align: center;
  position: fixed;
  z-index: 55;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &__title {
    font-size: 2.8rem;
    font-weight: 600;
    color: $color-text;
    padding: 1rem;
    background: $color-primary;
  }

  &__desc {
    font-size: 1.6rem;
    color: $color-text;
    padding: 1.5rem;
  }

  button {
    margin: $gap-small auto;
  }
}
