.queries {
  @media only screen and (max-width: $bp-medium) {
    grid-row: 1/2;
  }

  button {
    display: block;
    width: 100%;
    background: none;
    border: none;
    border-radius: $border-radius;
    text-align: left;
    font-size: 1.8rem;
    color: $color-text;
    padding: 1rem 2.5rem;
    margin: 0.5rem 0;
    position: relative;

    @media only screen and (max-width: $bp-small) {
      padding: 0.5rem 1.5rem;
    }

    &.queries--active {
      background: $color-primary;
      color: $color-white;
    }

    span {
      display: none;
    }

    &:hover,
    &:focus {
      cursor: pointer;
      background: $color-grey-lightest;

      span {
        display: block;
        background: none;

        svg {
          cursor: pointer;
          display: block;
          width: 2.4rem;
          height: 2.4rem;
          padding: 0.3rem;
          stroke: $color-grey-dark;
          border-radius: 50%;
          position: absolute;
          top: 50%;
          right: 1rem;
          transform: translateY(-50%);
          z-index: 10;

          &:hover {
            background: $color-primary;
            stroke: $color-white;
          }
        }
      }

      &.queries--active {
        background: lighten($color-primary, 5%);
        color: $color-white;

        span svg {
          stroke: $color-white;

          &:hover {
            background: $color-white;
            stroke: $color-primary;
          }
        }
      }
    }
  }
}
